<template>
  <!--  -->
  <div :class="classes">
    <div :class="wrapClasses">
      <a :class="logoClasses">
        <img src="/static/images/logo.png" />
      </a>
      <nav>
        <iMenu mode="horizontal" theme="dark" :active-name="0">
          <iMenuItem v-for="(nav, i) in navList" :key="i" :name="i" @click.native="handleClick(nav.path, $event)">
            {{nav.text}}
            <span :class="itemActiveTip"></span>
          </iMenuItem>
        </iMenu>
      </nav>
      <nav :class="oprLinkClasses">
        <div v-if = "isCookies" >
          <UserPhoto v-if = "isConsole" slot="other" :userName="userName" ></UserPhoto>
          <ul v-else class="ivu-menu ivu-menu-dark ivu-menu-horizontal">
            <li class="ivu-menu-item"><a href="/console"></a></li>
          </ul>
        </div>
        <div v-else >
          <slot name="other" ></slot>
        </div>
      </nav>
    </div>  
  </div>
</template>

<script>
  import {docsUrl} from '@/utils/staticUrl'
  import iMenu from 'iview/src/components/menu'
  const prefixCls = 'gtu-nav'

  export default {
    name: 'navbar',
    components: {
      iMenu,
      iMenuItem: iMenu.Item,
      SubMenu: iMenu.Submenu,
      UserPhoto: () => import(/* webpackChunkName:"common" */'@/components/userphoto')
    },
    props: {
      navList: {
        type: Array,
        required: true
      }
    },
    data () {
      return {
        userName: '',
        isCookies: false,
        isConsole: false
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      wrapClasses () {
        return [`${prefixCls}-wrap`, 'gtu-container']
      },
      logoClasses () {
        return [`${prefixCls}-logo`]
      },
      itemActiveTip () {
        return [`${prefixCls}-item-active-tip`]
      },
      oprLinkClasses () {
        return [`${prefixCls}-opr-link`]
      }
    },
    beforeCreate () {
      /* cookie */
      this.$http.get('/pub/checkUser/isOrNoLogin', '').then(
        function (response) {
          if (response.data.flag) {
            var rpath = this.$route.path
            if (rpath.indexOf('console') > 0) {
              this.isConsole = true
              this.userName = response.data.obj.user_name
            }
            this.isCookies = true
          } else {
            this.isCookies = false
          }
        },
        function (response) {
          this.isCookies = false
        }
      )
    },
    methods: {
      handleClick (path, event) {
        /*  */
        if (/^\/docs$/.test(path)) {
          !event.target.classList.contains('ivu-menu-item-active') && window.open(`${docsUrl}`, '_doc')
          return
        }
        this.$router.push({
          path
        })
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-nav";
  
  $navBgColor: #4F5052;
  $navHeight: 60px;
  $horizontalGap: 20px;
  $navItemActiveBorderWidth: 26px;
  $navItemActiveBorderHeight: 3px;
  $navItemActiveBorderColor: #69AC46;
  
  
  .ivu-menu {
    background: $navBgColor !important;
    &-item {
      position: relative;
    }
    &-item-active {
      color: $navItemActiveBorderColor !important; 
    }
  }

  #{$prefixCls} {
    position: fixed;
    z-index: 1000;
    top: 0;
    width: 100%;
    background-color: $navBgColor;
    &-wrap {
      display: flex;
      align-items: center;
      // padding: 0 $horizontalGap;
    }
    &-logo {
      font-size: 0;
      margin-right: $horizontalGap*2;
      img {
        max-height: $navHeight - 10;
      }
    }
    &-item-active-tip {
      position: absolute;
      bottom: 0;
      left: 50%;
      margin-left: -$navItemActiveBorderWidth/2;
      width: $navItemActiveBorderWidth;
      height: 0;
      border: 0px solid $navItemActiveBorderColor;
      display: none;
      .ivu-menu-item-active & {
        border-top-width: $navItemActiveBorderHeight;
        display: block;
      }
    }
    &-opr-link {
      margin-left: auto;  /* flex */  
      .ivu-menu-item {
        padding: 0 15px;
      }
      a {
        color: rgba(255,255,255,.7);
        &:hover {
          color: #fff;
        }
      }
    }
  }
  
</style>


